(function (window, document, $) {
	'use strict';

	var AXIL = {
		i: function (e) {
			AXIL.s();
			AXIL.methods()
		},

		s: function (e) {
			this._window = $(window),
				this._document = $(document),
				this._body = $('body');
		},

		methods: function (e) {
			AXIL.w();
			AXIL.clickDoc();
			AXIL.click();
			AXIL._dynamicActiveClass();
			AXIL.d();
			AXIL.smoothScroll();

		},

		w: function (e) {
			this._window.on('load', AXIL.l).on('scroll', AXIL.scrl).on('resize', AXIL.res)
		},

		l: function (e) {
			// alert();
		},

		clickDoc: function (e) {


		},

		click: function (e) {
			AXIL.smoothScroll();
		},

		_dynamicActiveClass: function (e) {
			$(function () {
				var _nav = $('.navbar-nav');
				var _activeLink = _nav.find("li a");

				var pgurl = window.location.href.substr(window.location.href
					.lastIndexOf("/") + 1);
				_activeLink.each(function () {
					if ($(this).attr("href") == pgurl || $(this).attr("href") == '') {
						$(this).addClass("active");

						if ($(this).parents('li').hasClass('has-dropdown')) {
							$(this).parents('.has-dropdown').find('>a').addClass('active');
						}
					}
				})
			});
		},

		c: function (e) {

		},
		//SMOOTHSCROLL
		smoothScroll: function (e) {
			$('a[href*="#"]')
				.not('[href="#"]')
				.not('[href="#0"]')
				.on('click', function (event) {
					var _defaults = {
						offset: 0
					}

					if (
						location.pathname.replace(/^\//, '') == this.pathname.replace(/^\//, '') &&
						location.hostname == this.hostname
					) {
						//set target
						var target = $(this.hash);

						//set the offset value
						var _tOffset = target[0].hasAttribute('data-scroll-offset') ? target.data('scroll-offset') : _defaults.offset;

						target = target.length ? target : $('[name=' + this.hash.slice(1) + ']');

						if (target.length) {
							event.preventDefault();
							$('html, body').animate({
								scrollTop: target.offset().top - _tOffset
							}, 800, 'easeInOutExpo');
						}
					}

				});
		},
		d: function (e) {

			$.fn.newFunction = function (options) {

				options = $.extend({
					name: "Ahsan",
					age: "25",
					profession: "designer"
				}, options);

				return this.each(function () {
					console.log('My name is ' + options.name + '. I am ' + options.age + ' years old and I am a ' + options.profession + '.');
				}); // end each
			};

			$('body').newFunction({
				name: "Xara",
				age: "65",
				profession: 'teacher'
			});
		}
	};
	AXIL.i()
})(window, document, jQuery);